import React from "react"
import { Typography } from "@material-ui/core"
import {OverridableComponent} from "@material-ui/core/OverridableComponent";
import {TypographyTypeMap} from "@material-ui/core/Typography/Typography";
import {Variant} from "@material-ui/core/styles/createTypography";

export const P: OverridableComponent<TypographyTypeMap> = ({ variant  = "body1", ...props }) => {
  return <Typography {...props} variant={variant as Variant} paragraph />
}
export const H1: OverridableComponent<TypographyTypeMap> = ({ variant = "h1", ...props }) => {
  return <Typography {...props} variant={variant as Variant} />
}
export const H2: OverridableComponent<TypographyTypeMap> = ({ variant = "h2", ...props }) => {
  return <Typography {...props} variant={variant as Variant} />
}
export const H3: OverridableComponent<TypographyTypeMap> = ({ variant = "h3", ...props }) => {
  return <Typography {...props} variant={variant as Variant} />
}
export const H4: OverridableComponent<TypographyTypeMap> = ({ variant = "h4", ...props }) => {
  return <Typography {...props} variant={variant as Variant} />
}
export const H5: OverridableComponent<TypographyTypeMap> = ({ variant = "h5", ...props }) => {
  return <Typography {...props} variant={variant as Variant} />
}
export const H6: OverridableComponent<TypographyTypeMap> = ({ variant = "h6", ...props }) => {
  return <Typography {...props} variant={variant as Variant} />
}
