const drawerWidth = "50%";

// Avoid URL injection
const safeFontName = (fontName: string) => fontName.replace(/[^a-zA-Z0-9\s]/g, '')

const safeCssSize = (cssSize: string | number) => {
  switch (typeof cssSize) {
    case "string": {
      if (/^\s*\d+\.?\d*\s*(px|em|rem|%)\s*?$/.test(cssSize)) {
        return cssSize;
      } else {
        return "1rem";
      }
    }
    case "number": return cssSize;
    default: return "1rem";
  }
}

const safeCssFontWeight = (cssFontWeight: string | number) => {
  switch (typeof cssFontWeight) {
    case "string": {
      switch (cssFontWeight) {
        case "100":
        case "200":
        case "300":
        case "400":
        case "500":
        case "600":
        case "700":
        case "800":
        case "900":
        case "950":
          return parseInt(cssFontWeight);
        case "normal":
        case "bold":
        case "bolder":
        case "lighter":
          return cssFontWeight;
        default: return "normal";
      }
    }
    case "number": return cssFontWeight;
    default: return "normal";
  }
}

const safeCssColor = (cssColor: unknown) => {
  switch (typeof cssColor) {
    case "string": {
      // standard hex colors
      if (/^#([0-9a-fA-F]{3}){1,2}$/.test(cssColor)) {
        return cssColor;
      // Word colors
      } else if (/^[a-zA-Z]+$/.test(cssColor)) {
        return cssColor;
      // rgb() or rgba()
      } else if (/^\s*rgba?\s*\((\d*\.?\d+\s*,\s*){2}(\d*\.?\d+\s*)(,\s*\d*\.?\d+)?\s*\)$/.test(cssColor)) {
        return cssColor;
      } else {
        return "white";
      }
    }
    default: return "white";
  }
}

