import React, {FC, useContext} from "react";
import {Paper, TextField} from "@material-ui/core";
import {ThemeSettings} from "../ThemeSettings";

interface GeneralEditorProps {
  elementName: 'backgroundColor',
  label: string,
  onChange?: (newValue: string) => void
}

const GeneralEditor: FC<GeneralEditorProps> = (props) => {
  const [themeSettings, setThemeSettings] = useContext(ThemeSettings)

  const onChange = (value: string) => {
    const newThemeSettings = {
      ...themeSettings,
      [props.elementName]: value
    };
    setThemeSettings(newThemeSettings);
  }

  return <Paper>
    <TextField id={`${props.elementName}`} label={props.label} variant="standard"
               value={themeSettings[props.elementName]}
               onChange={(e) => onChange(e.target.value)}/>
  </Paper>
}

export default GeneralEditor
