import React, {FC, useContext} from "react";
import {Paper, NativeSelect, Typography, TextField, InputLabel, FormControl} from "@material-ui/core";
import {ThemeSettings} from "../ThemeSettings";

interface FormatEditorValue {
  fontSize: string,
  fontFamily: string,
  fontWeight: string
}

interface FormatEditorProps {
  elementName: 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6' | 'p',
  onChange?: (newValue: FormatEditorValue) => void
}

const FormatEditor: FC<FormatEditorProps> = (props) => {
  const [themeSettings, setThemeSettings] = useContext(ThemeSettings)

  const onChange = (fieldName: string, value: string) => {
    const newThemeSettings = {
      ...themeSettings,
      [props.elementName]: {
        ...themeSettings[props.elementName],
        [fieldName]: value
      }
    };
    setThemeSettings(newThemeSettings);
  }

  return <Paper>
    <Typography variant="h4" component="h1">{props.elementName}</Typography>
    <TextField id={`${props.elementName}-font-name`} label="Font Name" variant="standard"
               value={themeSettings[props.elementName].fontFamily}
               onChange={(e) => onChange('fontFamily', e.target.value)}/>
    <TextField id={`${props.elementName}-font-size`} label="Size" variant="standard"
               value={themeSettings[props.elementName].fontSize}
               onChange={(e) => onChange('fontSize', e.target.value)}/>
    <FormControl>
      <InputLabel htmlFor={`${props.elementName}-font-weight`}>Font Weight</InputLabel>
      <NativeSelect id={`${props.elementName}-font-weight`}
                 value={themeSettings[props.elementName].fontWeight}
                 onChange={(e) => onChange('fontWeight', e.target.value)}>
        <option value={100}>100 (Thin)</option>
        <option value={200}>200 (Extra Light)</option>
        <option value={300}>300 (Light)</option>
        <option value={400}>400 (Normal)</option>
        <option value={500}>500 (Medium)</option>
        <option value={600}>600 (Semi Bold)</option>
        <option value={700}>700 (Bold)</option>
        <option value={800}>800 (Extra Bold)</option>
        <option value={900}>900 (Black)</option>
        <option value={950}>950 (Extra Black)</option>
      </NativeSelect>
    </FormControl>
    <TextField id={`${props.elementName}-color`} label="Colour" variant="standard"
               value={themeSettings[props.elementName].color}
               onChange={(e) => onChange('color', e.target.value)}/>
  </Paper>
}

export default FormatEditor
