import React from "react";
import {createMuiTheme} from "@material-ui/core";

type ThemeSettingsType = {
  [key in 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6' | 'p']: {
    fontSize: string,
    fontWeight: number | "bold" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | "normal" | "bolder" | "lighter",
    fontFamily: string,
    color: string
  };
} & {
  backgroundColor: string;
};

const materialTheme = createMuiTheme();

export const DEFAULT_THEME: ThemeSettingsType = {
  backgroundColor: materialTheme.palette.background.paper,
  h1: {
    fontSize: materialTheme.typography.h1.fontSize as string,
    fontWeight: materialTheme.typography.h1.fontWeight as number | "bold" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | "normal" | "bolder" | "lighter",
    fontFamily: "Roboto",
    color: materialTheme.palette.text.primary
  },
  h2: {
    fontSize: materialTheme.typography.h2.fontSize as string,
    fontWeight: materialTheme.typography.h2.fontWeight as number | "bold" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | "normal" | "bolder" | "lighter",
    fontFamily: "Roboto",
    color: materialTheme.palette.text.primary
  },
  h3: {
    fontSize: materialTheme.typography.h3.fontSize as string,
    fontWeight: materialTheme.typography.h3.fontWeight as number | "bold" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | "normal" | "bolder" | "lighter",
    fontFamily: "Roboto",
    color: materialTheme.palette.text.primary
  },
  h4: {
    fontSize: materialTheme.typography.h4.fontSize as string,
    fontWeight: materialTheme.typography.h4.fontWeight as number | "bold" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | "normal" | "bolder" | "lighter",
    fontFamily: "Roboto",
    color: materialTheme.palette.text.primary
  },
  h5: {
    fontSize: materialTheme.typography.h5.fontSize as string,
    fontWeight: materialTheme.typography.h5.fontWeight as number | "bold" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | "normal" | "bolder" | "lighter",
    fontFamily: "Roboto",
    color: materialTheme.palette.text.primary
  },
  h6: {
    fontSize: materialTheme.typography.h6.fontSize as string,
    fontWeight: materialTheme.typography.h6.fontWeight as number | "bold" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | "normal" | "bolder" | "lighter",
    fontFamily: "Roboto",
    color: materialTheme.palette.text.primary
  },
  p: {
    fontSize: materialTheme.typography.body1.fontSize as string,
    fontWeight: materialTheme.typography.body1.fontWeight as number | "bold" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | "normal" | "bolder" | "lighter",
    fontFamily: "Roboto",
    color: materialTheme.palette.text.primary
  },
}

export const ThemeSettings = React.createContext<[typeof DEFAULT_THEME, (theme: typeof DEFAULT_THEME) => void]>([DEFAULT_THEME, () => {}]);
